(function() {
  'use strict';

  angular
    .module('patientCare', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr', 'oitozero.ngSweetAlert']);

})();

(function() {
  'use strict';

  // best practice to create another module for helper directives (such as `utility`) but
  // for the purpose of this exercise, it should be fine
  angular
    .module('patientCare')
    .directive('removeAnimationAfterload', removeAnimationAfterload);

  /** @ngInject */
  function removeAnimationAfterload($timeout) {
     return {
       restrict: "A",
       scope: {
        animateClass: "@"
       },
       link: function(scope, element, attrs) {
        angular.element(document).ready(function() {
          var animationEnd = 'webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend';

          angular.forEach(element, function(ele, ind) {
              $(ele).addClass(attrs.animateClass).one(animationEnd, function() {
                    $(ele).removeClass(attrs.animateClass);
                });
          })

        });
       }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('patientCare')
    .factory('Provider', ProviderFactory);

  /** @ngInject */
  function ProviderFactory($resource) {
    return $resource("/api/v1/providers/:id", { id: "@id" }, {
      update: {
        method: "PUT"
      }
    });
  }

})();

(function() {
  'use strict';

  // best practice to create another module for helper directives (such as `utility`) but
  // for the purpose of this exercise, it should be fine
  angular
    .module('patientCare')
    .directive('numberValidation', numberValidation);

  /** @ngInject */
  // better way is to use input type as `number` but it requires some reformat of
  // number generated from the seed.rb in the database
  // http://stackoverflow.com/questions/14419651/filters-on-ng-model-in-an-input/14425022#14425022
  function numberValidation() {
     return {
       require: 'ngModel',
       link: function(scope, element, attrs, modelCtrl) {
         modelCtrl.$parsers.push(function (inputValue) {

           var transformedInput = inputValue.toLowerCase().replace(/[a-z]/g, '');

           if (transformedInput!=inputValue) {
             modelCtrl.$setViewValue(transformedInput);
             modelCtrl.$render();
           }

           return transformedInput;
         });
       }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('patientCare')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

