(function() {
  'use strict';

  angular
    .module('patientCare')
    .controller('ProvidersUpdateController', ProvidersUpdateController);

  /** @ngInject */
  function ProvidersUpdateController(Provider, $stateParams,  $state, $scope, toastr) {

    $scope.providesOptions = ["Diabetes Care","Dialysis","Medication Management","Outpatient Therapy","Oxygen","Physical Therapy","Speech Therapy","Wound Care"]

      Provider.get({ id: $stateParams.id }, function(provider) {
        $scope.provider = provider;
        $scope.copiedProvides = $scope.provider.provides;

        // due to Rails API `provides` as an array data structure, update needs to be reverted
        // to an object (very weird), otherwise there is some weird behavior
        $scope.provider.provides = {};

        for (var i = 0; i < $scope.copiedProvides.length; i++) {
          var index = $scope.providesOptions.indexOf($scope.copiedProvides[i]);
          $scope.provider.provides[index] = $scope.copiedProvides[i];
        }

        $scope.updateProvider = function() {
          var providesArr = [];
          var providesObj = $scope.provider.provides;
          for (var key in providesObj) {
            if (providesObj.hasOwnProperty(key)) {
              providesArr.push(providesObj[key]);
            }
          }

          $scope.provider.provides = providesArr;
          console.log("updated provides: ", $scope.provider.provides)
          $scope.provider.$update(function() {
            $state.go('providerIndex');
            toastr.success($scope.provider.name + " provider has been updated!", "Updated Successfully:")
          });
        };
      });
    };
})();

(function() {
  'use strict';

  angular
    .module('patientCare')
    .controller('ProvidersIndexController', ProvidersIndexController);

  /** @ngInject */
  function ProvidersIndexController(Provider, $uibModal, $state, $scope, $window, toastr, SweetAlert) {
    var vm = this;
    vm.providers = Provider.query();

    // vm.open = function() {
    //   var modalInstance = $uibModal.open({
    //     templateUrl: "app/components/modal/modal.html",
    //     controller: "ModalCtrl",
    //     controllerAs: "modal",
    //     scope: $scope
    //   })
    // };

    // DELETE /api/v1/providers/:id
    $scope.deleteProvider = function(provider) {

      SweetAlert.swal({
       title: "Are you sure you want to delete " + provider.name +"?",
       text: "You will not be able to recover this provider!",
       type: "warning",
       showCancelButton: true,
       confirmButtonColor: "#DD6B55",confirmButtonText: "Yes, delete it!",
       cancelButtonText: "No, cancel it!",
       closeOnConfirm: false,
       closeOnCancel: false },
       function(isConfirm){
         if (isConfirm) {
          provider.$delete(function() {
            vm.providers = Provider.query();  // reload data
            SweetAlert.swal("Deleted!", "The provider has been deleted!", "success");
            // toastr.success(provider.name + " provider has been deleted!", "Deleted Successfully:");
          });
        } else {
          SweetAlert.swal("Cancelled", "Your provider is not deleted!", "error");
        }
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('patientCare')
    .controller('ProvidersCreateController', ProvidersCreateController);

  /** @ngInject */
  function ProvidersCreateController(Provider, $state, $scope, toastr) {
    var vm = this;

    $scope.providesOptions = ["Diabetes Care","Dialysis","Medication Management","Outpatient Therapy","Oxygen","Physical Therapy","Speech Therapy","Wound Care"]

    $scope.provider = new Provider();

    $scope.addProvider = function() {

      // reformat data to send to server
      // since the server only accepts `provider.provides` as an array
      // but the checkbox is giving an object

      // THESE CODES NEED TO BE REFACTOR INTO A FILTER SINCE IT'S ALSO BEING USED
      // IN PROVIDERUPDATE.CONTROLLER.JS
      var providesArr = [];
      var providesObj = $scope.provider.provides;
      for (var key in providesObj) {
        if (providesObj.hasOwnProperty(key)) {
          providesArr.push(providesObj[key]);
        }
      }

      $scope.provider.provides = providesArr;

      $scope.provider.$save(function() {
        $state.go('providerIndex');
        toastr.success($scope.provider.provider.name + " provider has been created!", "Created Successfully:")
      });
    };
  }
})();
